"use client"
import DishCard from '@/components/Dashboard/DishCard';
import { useAppSelector } from '@/hooks/reduxHooks';
import { Dish } from '@/redux/reduxTypes';
import axios from 'axios';
import { useParams } from 'next/navigation';
import React, { useEffect, useState } from 'react'

const Page = () => {

    const { id } = useParams() as { id: string }
    const [info, setInfo] = useState<string>();
    const {user} = useAppSelector(state=>state.auth);
    useEffect(() => {
        const getTextAbout = async () => {
            try {
                const res = await axios.post("http://localhost:5200/api/about", { id });
                setInfo(res.data);
            } catch (err) {
                console.error(err);
            }

        }
    }, [])
    return (
        <div className='flex flex-col gap-5 mb-12'>
            <div className="flex items-center">
            <h1 className='section-title'>About</h1>
            {user?.role=="admin" &&(
                <button className=''><></></button>
            )}
            </div>
            {info?(
            <p className="text-lg leading-5 font-medium mt-6">{info}</p>

            ): (
                <span className='text-lg leading-5 font-medium text-center mt-6'>No info yet!</span>
            )}

        </div>
    )
}

export default Page